@font-face {
  font-family: "Helios";
  src: local("Helios Regular"), local("Helios-Regular"), url("../fonts/HeliosC/HeliosC.woff2") format("woff2"), url("../fonts/HeliosC/HeliosC.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Helios";
  src: local("Helios Bold"), local("Helios-Bold"), url("../fonts/HeliosC/HeliosC-Bold.woff2") format("woff2"), url("../fonts/HeliosC/HeliosC-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
:root {
  --white: #FFFFFF;
  --main-blue: #003264;
  --blue: #187CD3;
  --dark: #242627;
  --grey82: #828282;
  --grey64: #646464;
  --grey4F: #4f4f4f;
  --greyF2: #f2f2f2;
  --greyF9: #f9f9f9;
  --grey: #C3C8C8;
  --orangeE9: #E9862A;
  --orangeE7: #E77E1E;
  --stroke: #EBEBEB;
}

* {
  box-sizing: border-box;
  color: var(--dark);
  font-family: "Helios", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
}

body {
  padding: 0;
  margin: 0;
}

.container {
  max-width: 1180px;
  margin: 0 auto;
}

.big-container {
  max-width: 1482px;
  margin: 0 auto;
}

picture,
img {
  display: block;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0 0;
  font-family: "Helios", sans-serif;
  font-weight: 700;
}

p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  padding: 0;
  border: 0;
  background: transparent;
  cursor: pointer;
}

.button {
  width: fit-content;
  padding: 22px 63px;
  background-color: var(--orangeE9);
  color: var(--white);
  transition: background-color 0.6s, color 0.6s, box-shadow 0.1s;
}
.button:hover {
  background-color: var(--orangeE7);
}
.button:disabled {
  background-color: var(--grey);
}

.button-more {
  display: flex;
  align-items: center;
  border: 1px solid var(--stroke);
  padding: 19px 30px;
  color: var(--blue);
  font-weight: 40;
  font-size: 14px;
  line-height: 17px;
  transition: border 0.1s ease-in-out;
}
.button-more__icon {
  display: block;
  margin-left: 36px;
}
.button-more__icon > svg {
  height: 8px;
  width: 5px;
}
.button-more:hover {
  border: 1px solid #BDBDBD;
}

.vk {
  width: 20px;
}

.ok {
  height: 18px;
}

.fb {
  height: 17px;
}

.inst {
  width: 15px;
}

.arrow-right {
  width: 10px;
}

.close-icon {
  display: block;
  width: 11px;
}

.play {
  width: 104px;
  height: 104px;
}

.trash {
  width: 10px;
}

.check-mark {
  width: 12px;
}

.label {
  position: relative;
  display: block;
  width: 100%;
  background-color: var(--greyF2);
  cursor: text;
}

.placeholder {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 28px;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: var(--grey82);
  transition: font-weight 0.1s ease-in-out;
  transition: top 0.1s ease-in-out;
}
.placeholder--min {
  top: 12px;
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
}

.input {
  border: none;
  width: 100%;
  padding: 25px 28px;
  background-color: transparent;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
}
.input:hover::placeholder {
  transition: color 0.3s;
  color: var(--orangeFF);
}
.input:focus {
  outline: none;
}
input[type=date] {
  position: relative;
  width: 100%;
  height: 100%;
  background: transparent url("./img/sprite.svg#date") no-repeat no-repeat right center;
  background-size: 72px 16px;
}

input[type=date]::-webkit-calendar-picker-indicator {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: auto;
  height: auto;
  color: transparent;
  background: transparent;
}

input[type=date]::-webkit-inner-spin-button,
input[type=date]::-webkit-clear-button {
  z-index: -10;
}

input[type=date]::-webkit-input-placeholder {
  z-index: 99;
}

input[type=date]:placeholder {
  z-index: 99;
}

input[type=date]:not(.has-value):before {
  content: attr(placeholder);
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: var(--grey82);
  width: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.section-title {
  font-weight: 400;
  font-size: 36px;
  line-height: 44px;
}

.link {
  color: var(--blue);
}

@media (max-width: 1325px) {
  .container,
.big-container {
    padding: 0 31px;
  }
}
@media (max-width: 1325px) and (max-width: 1000px) {
  .section-title {
    font-weight: 700;
    font-size: 22px;
    line-height: 22px;
  }
}
@media (max-width: 1325px) and (max-width: 831px) {
  .container,
.success-stories {
    padding: 0 40px;
  }
}
.about-us {
  margin-bottom: 70px;
}
.about-us__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
}
.about-us__swiper {
  display: none;
}

.company-card {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  columns: 1;
  max-width: 398px;
  transition: box-shadow 0.1s ease;
  padding: 82px 50px;
  box-shadow: 0px 0px 0px 1px #EDEDED;
}
.company-card__icon {
  margin-bottom: 45px;
  transition: transform 0.1s ease-in-out;
  transform: translateY(50%);
}
.company-card__icon > svg {
  width: 60px;
  fill: #4F4F4F;
  transition: fill 0.1s ease-in-out;
}
.company-card__title {
  transform: translateY(50%);
  margin-bottom: 23px;
  font-weight: 400;
  font-size: 20px;
  line-height: 25px;
  transition: transform 0.1s ease-in-out;
}
.company-card__text {
  transition: opacity 0.3s ease-in-out;
  color: var(--grey64);
  text-align: center;
  opacity: 0;
}
.company-card:hover {
  box-shadow: 0px 17px 46px rgba(0, 0, 0, 0.08);
  background-color: var(--white);
}
.company-card:hover > .company-card__icon {
  transform: translateY(0);
}
.company-card:hover > .company-card__icon > svg {
  fill: var(--orangeE9);
}
.company-card:hover .company-card__title {
  transform: translateY(0);
}
.company-card:hover .dynamics * {
  stroke: var(--orangeE9);
}
.company-card:hover > .company-card__text {
  opacity: 1;
}

.dynamics {
  fill: transparent;
}
.dynamics * {
  transition: stroke 0.1s ease-in-out;
  stroke: #4F4F4F;
  stroke-width: 3px;
}

@media (max-width: 915px) {
  .about-us {
    margin-bottom: 121px;
  }
  .about-us__list {
    display: none;
  }
  .about-us__swiper {
    display: block;
  }
  .swiper-pagination--about-us {
    bottom: -50px;
  }
}
.burger-open,
.burger {
  display: none;
}

@media (max-width: 1100px) {
  .nav__item:not(:last-child) {
    margin-right: 30px;
  }
}
@media (max-width: 1100px) and (max-width: 915px) {
  .burger-open {
    display: block;
    width: 28px;
  }
  .burger {
    position: fixed;
    top: 0;
    z-index: 10000;
    width: 100%;
    height: fit-content;
    padding: 68px 40px;
    background-color: var(--dark);
    box-shadow: 0px 0px 0px 200vw rgba(36, 38, 39, 0.84);
  }
  .burger__close {
    position: absolute;
    top: 40px;
    right: 40px;
    display: block;
  }
  .burger__close.button {
    padding: 27px;
  }
  .burger > .nav {
    display: block;
  }
  .burger > .nav > .nav__list {
    display: grid;
  }
  .nav__item:not(:last-child) {
    margin-bottom: 40px;
  }
  .nav__link {
    width: fit-content;
    color: var(--white);
  }
  .nav__link:hover {
    color: var(--orangeE9);
  }
  .nav__link::after {
    display: none;
  }
  .social {
    transition: border 0.3s ease;
  }
  .social__icon--white {
    transition: fill 0.3s ease;
    fill: var(--white);
  }
  .social:hover {
    border: 1px solid var(--white);
  }
  .social:hover > .social__icon {
    fill: var(--orangeE9);
  }
  .social:active {
    border: 1px solid var(--white);
  }
}
.contacts__map {
  position: relative;
  width: 100%;
  height: 632px;
}
.contacts__content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  columns: 1;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 100;
}
.contacts__title {
  margin-bottom: 19px;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
}
.contacts__tel {
  display: block;
  margin-bottom: 16px;
  font-weight: 400;
  font-size: 64px;
  line-height: 75px;
}
.contacts__email {
  margin-bottom: 50px;
}
.contacts__join-team {
  margin-bottom: 49px;
}

[class*=ymaps-2][class*=-ground-pane] {
  filter: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg'><filter id='grayscale'><feColorMatrix type='matrix' values='0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0 0 0 1 0'/></filter></svg>#grayscale");
  -webkit-filter: grayscale(100%);
}

.ymaps-2-1-79-map-copyrights-promo,
.ymaps-2-1-79-copyright,
.ymaps-2-1-79-copyright_logo_no {
  display: none !important;
}

.socials__list--contacts {
  max-width: fit-content;
  grid-template-columns: repeat(4, 1fr);
  gap: 16px;
}
.socials__icon--orange {
  fill: var(--orangeE9);
}

@media (max-width: 915px) {
  .contacts__map {
    height: 506px;
  }
  .contacts__tel {
    display: block;
    white-space: nowrap;
    margin-bottom: 16px;
    font-weight: 400;
    font-size: 56px;
    line-height: 69px;
  }
}
#footer__trigger {
  position: relative;
  bottom: 65vh;
  z-index: 10000;
}

.footer {
  padding-top: 40px;
  padding-bottom: 22px;
  overflow: hidden;
}
.footer__container > * {
  text-align: center;
}
.footer__logo {
  width: 80px;
  margin: 0 auto;
  opacity: 0.5;
  filter: grayscale(100%);
}
.footer__logo > .logo__img {
  width: 80px;
}

.company-name {
  margin-bottom: 17px;
  font-weight: 400;
  font-size: 11px;
  line-height: 16px;
  color: var(--grey82);
}

.private-policy {
  display: block;
  width: 100%;
  margin-bottom: 33px;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
}
.for-students {
  position: relative;
  margin-bottom: 90px;
}
.for-students__container {
  padding: 86px 0 104px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  columns: 1;
}
.for-students__bacg {
  position: absolute;
  height: 100%;
  width: 50%;
  background-color: var(--greyF9);
  z-index: -1;
}
.for-students__bacg--left {
  left: 0;
  border-radius: 0px 36px 36px 0px;
}
.for-students__bacg--rigth {
  right: 0;
  border-radius: 36px 0px 0px 36px;
}
.for-students__title {
  margin-bottom: 28px;
}
.for-students__text {
  font-weight: 400;
  font-size: 22px;
  line-height: 28px;
}
.for-students__text, .for-students__list {
  margin-bottom: 84px;
}
.for-students__feedback {
  font-weight: 400;
  font-size: 22px;
  line-height: 26px;
}
.for-students__feedback > * {
  font-weight: 400;
  font-size: 22px;
  line-height: 26px;
}

.skills {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 116px;
}
.skills__item {
  margin-bottom: 39px;
}

.skill {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  columns: 1;
}
.skill__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  columns: 1;
  background-color: var(--white);
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-bottom: 39px;
}
.skill__icon > svg {
  width: 30px;
}
.skill__name {
  display: block;
  white-space: nowrap;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
}

@media (max-width: 1235px) {
  .skills {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    column-gap: 131px;
    row-gap: 60px;
  }
  .skills__item {
    margin-bottom: 39px;
  }
}
.header {
  color: var(--dark);
  background-color: var(--white);
  margin-bottom: 36px;
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 35px;
  padding-bottom: 17px;
}
.header__line {
  display: block;
  height: 2px;
  width: 100%;
  background-color: #f2f2f2;
}

.logo {
  display: block;
}
.logo__img {
  width: 115px;
}

.nav__list {
  display: flex;
}
.nav__item:not(:last-child) {
  margin-right: 64px;
}
.nav__link {
  outline: none;
  display: block;
  transition: color 0.3s ease;
}
.nav__link::after {
  content: "";
  display: block;
  opacity: 0;
  position: relative;
  top: 33px;
  left: 50%;
  transform: translateX(-50%);
  height: 2px;
  width: calc(100% + 10px);
  background-color: var(--orangeE9);
  transition: opacity 0.3s ease;
}
.nav__link:hover.nav__link::after {
  opacity: 1;
}

.socials {
  position: relative;
}
.socials--modal {
  z-index: 100;
}
.socials__list {
  display: grid;
}
.socials__list--header {
  position: absolute;
  top: 34px;
  right: -25px;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  padding: 19px;
  box-shadow: 0px 17px 46px rgba(0, 0, 0, 0.08);
  background-color: var(--white);
  opacity: 0;
  pointer-events: none;
  transform: scale(0);
}
.socials__list--visible {
  display: grid;
}
.socials__btn-open {
  position: relative;
}
.socials__btn-open::after {
  content: "";
  position: absolute;
  top: 55%;
  right: -13px;
  transform: translateY(-50%);
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 8px solid #242627;
}

.social {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 68px;
